/* styles/Image.module.css */
.imageContainer {
  background-color: rgb(255, 115, 37);
  border-radius: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.image {
  border-radius: 20px;
  width: 100%;
  height: 100%;  
  object-fit: cover;
}
